import React from 'react';
import ReactDOM from 'react-dom';
import Modal from '../src';
import Button from '@gag/button';
import WhiteSpace from '@gag/white-space';
import WingBlank from '@gag/wing-blank';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: false,
    };
  }
  showModal = (e) => {
    //  x  button 
    //  x  (/)
    e.preventDefault(); //  Android 
    this.setState({
      visible: true,
    });
  }
  onClose = () => {
    this.setState({
      visible: false,
    });
  }
  render() {
    return (
      <div>
        <WhiteSpace />
        <WingBlank>
          <Button onClick={this.showModal}>Modal </Button>
        </WingBlank>
        <WhiteSpace />
        <Modal
          title=" title"
          transparent
          maskClosable={false}
          visible={this.state.visible}
          onClose={this.onClose}
          footer={[{ text: '', onPress: () => { console.log('ok'); this.onClose(); } }]}
        >
          ...<br />
          ...<br />
        </Modal>
      </div>
    );
  }
}

ReactDOM.render(<App />, document.getElementById('sk-root'));



